*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    outline: none;
}
.weatherBody{
    width: 500px;
    margin: 0 auto;
    height: auto;
    padding: 10px;
}
.topSearchBar{
    width: 100%;
    height: 30px;
    background-color: #77777754;
    border: 1px solid #dddddd54;
    display: flex;
    align-items: center;
    font-size: 1rem;
}
.topSearchBar > input {
    width: 80%;
    height: 100%;
    padding: 1px 10px;
    background-color: #77777700;
    border: none;
    color: #fff;
}
.topSearchBar > span {
    width: 20%;
    height: 100%;
    color: #fff;
    text-align: center;
    background-color: #ffffff1f;
    cursor: pointer;
}
.f{
    display: flex;
    align-items: center;
    justify-content: center;
}
.fc{
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.weatherBox{
    width: 100%;
    height: auto;
    margin: 20px 0px;
    border: 1px solid #dddddd54;
}
.weatherIcon{
    width: 100%;
    height: auto;
    background-color: #77777780;
}
#titleIcon{
    font-size: 5rem;
    padding: 10px;
}
.tempInfo{
    width: 100%;
    height: 80px;
}
.tempDegg{
    width: 70%;
    height: 100%;
    display: flex;
    background-color: #77777748;
}
.tempTime{
    width: 30%;
    height: 100%;
}
.degg{
    width: 50%;
    height: 100%;
    padding: 5px;
}
.dg{
    font-size: 2.5rem;
    font-weight: bold;
}
.temIn{
    font-size: 1.5rem;
    font-weight: 600;text-transform: uppercase;
}
.cnNam{
    font-size: 1rem;
}
.tempTime{
    padding: 5px;
    font-size: 1rem;
    background-color: #7777775e;
}

.detailsWeather{
    width: 100%;
    height: 60px;
}

.fourPices{
    width: 25%;
    height: 100%;
}
.half{
    width: 50%;
    height: 100%;
    font-size: 12px;
}
#btnIcn{
    font-size: 2rem;
}